body {
  background-color: #282c34;
  color: white;
  font-size: 16px;
}
.App-main {
  height: 60vh;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
@keyframes random {
  15% { border-color: red; }
  15% { box-shadow: 5px 5px 3px red; }
  30% { border-color: orange; }
  30% { box-shadow: 5px 5px 3px orange; }
  45% { border-color: yellow; }
  45% { box-shadow: 5px 5px 3px yellow; }
  60% { border-color: green; }
  60% { box-shadow: 5px 5px 3px green; }
  75% { border-color: blue; }
  75% { box-shadow: 5px 5px 3px blue; }
}
